import Vue from 'vue';
//配置路由
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.创建组件
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
    import yuanxi from '@/views/modules/yuanxi/list'
    import bubanshenqing from '@/views/modules/bubanshenqing/list'
    import biyesheng from '@/views/modules/biyesheng/list'
    import shixijiuye from '@/views/modules/shixijiuye/list'
    import xinxigenggaishenqing from '@/views/modules/xinxigenggaishenqing/list'
    import yuanxijiaoshi from '@/views/modules/yuanxijiaoshi/list'
    import zhengshijiuye from '@/views/modules/zhengshijiuye/list'


//2.配置路由   注意：名字
const routes = [{
    path: '/index',
    name: '首页',
    component: Index,
    children: [{
      // 这里不设置值，是把main作为默认页面
      path: '/',
      name: '首页',
      component: Home,
      meta: {icon:'', title:'center'}
    }, {
      path: '/updatePassword',
      name: '修改密码',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '支付',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '个人信息',
      component: center,
      meta: {icon:'', title:'center'}
    }
      ,{
	path: '/yuanxi',
        name: '院系',
        component: yuanxi
      }
      ,{
	path: '/bubanshenqing',
        name: '补办申请',
        component: bubanshenqing
      }
      ,{
	path: '/biyesheng',
        name: '毕业生',
        component: biyesheng
      }
      ,{
	path: '/shixijiuye',
        name: '实习就业',
        component: shixijiuye
      }
      ,{
	path: '/xinxigenggaishenqing',
        name: '信息更改申请',
        component: xinxigenggaishenqing
      }
      ,{
	path: '/yuanxijiaoshi',
        name: '院系教师',
        component: yuanxijiaoshi
      }
      ,{
	path: '/zhengshijiuye',
        name: '正式就业',
        component: zhengshijiuye
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '/',
    name: '首页',
    redirect: '/index'
  }, /*默认跳转路由*/
  {
    path: '*',
    component: NotFound
  }
]
//3.实例化VueRouter  注意：名字
const router = new VueRouter({
  mode: 'hash',
  /*hash模式改为history*/
  routes // （缩写）相当于 routes: routes
})
const originalPush = VueRouter.prototype.push
//修改原型对象中的push方法
VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}
export default router;
