							<el-button :style='{"border":"2px solid #33BAE9","cursor":"pointer","padding":"0 24px","margin":"0 10px 6px 0","outline":"none","color":"#33BAE9","borderRadius":"30px","background":"#fff","width":"auto","fontSize":"14px","height":"32px"}' v-if=" isAuth('biyesheng','修改')" type="primary" size="mini" @click="addOrUpdateHandler(scope.row.id)">修改</el-button>





							<el-button :style='{"border":"2px solid #33BAE9","cursor":"pointer","padding":"0 24px","margin":"0 10px 6px 0","outline":"none","color":"#33BAE9","borderRadius":"30px","background":"#fff","width":"auto","fontSize":"14px","height":"32px"}' v-if="isAuth('biyesheng','删除') " type="danger" size="mini" @click="deleteHandler(scope.row.id)">删除</el-button>
